import React from 'react'
import { Layout, Stack, Main, PreFooter, Sidebar } from '@layout'
import PageTitle from '@components/PageTitle'
import Pagination from '@components/Pagination'
import CardList from '@components/CardList'
import Divider from '@components/Divider'
import Seo from '@widgets/Seo'
import AuthorExpanded from '@widgets/AuthorExpanded'
import Sponsor from '@widgets/Sponsor'
import SponsorEvent from '@widgets/SponsorEvent'
import SponsorCommercial from '@widgets/SponsorCommercial'
import SponsorHuman from '@widgets/SponsorHuman'

const PageCollectionAuthors = ({
  data: { posts, collectionInfo },
  ...props
}) => (
  <Layout {...props}>
    <Seo title={collectionInfo.name} description={collectionInfo.description} />
    <Divider />
    {/* <Stack effectProps={{ effect: 'fadeInDown' }}>
      <PageTitle header='منشورات للكاتب' />
    </Stack> */}
    <Divider />
    <Stack>
      <Main>
        <AuthorExpanded author={collectionInfo} />
        <Divider />
        {posts.nodes && (
          <CardList
            nodes={posts.nodes}
            variant={['horizontal-md', 'vertical']}
            columns={[1, 2, 3, 3]}
          />
        )}
      </Main>
    </Stack>
    <Divider />
    <PreFooter>
      <Pagination {...posts.pageInfo} {...collectionInfo} />
    </PreFooter>
    <Stack>
      <Sidebar pl={4}>
        <Divider />
        <Sponsor />
      </Sidebar>
      <Sidebar pl={4}>
        <Divider />
        <SponsorHuman />
      </Sidebar>
      <Sidebar pl={4}>
        <Divider />
        <SponsorEvent />
      </Sidebar>
      <Sidebar pl={4}>
        <Divider />
        <SponsorCommercial />
      </Sidebar>
    </Stack>
  </Layout>
)

export default PageCollectionAuthors
